body {
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 500px;
  height: fit-content;
  padding: 0;
}

:root {
  --blue: #1585ff;
  --light-blue: #60adfb;
  --light-grey: #e4e4e4; 
  --dark-grey: #585858;
  --black: #272727;
  --primary-gradient-color: linear-gradient(to left, var(--light-blue), var(--blue));
}

root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button, input {
  border: none;
  outline: none;
  background-color: white;
  padding: 10px 20px;
  border-radius: 8px;
  border: 1px solid var(--light-grey);
}

button {
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  color: var(--black);
}

.initial-button {
  background-image: var(--primary-gradient-color) !important;
  color: white !important;
}

.outlined-button {
  border: 2px solid black;
  border-image: var(--primary-gradient-color) !important;
  background-color: transparent !important;
  color: var(--black) !important;
}

.primary-color-bg {
  background-image: var(--primary-gradient-color) !important;
  color: white !important;
}

.primary-color {
  color: var(--blue) !important;
}

.horizontal-wrap {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}